import React, { useEffect, useState } from "react";
import "./CartCheckoutSignin.css";
import Visibility from "@material-ui/icons/Visibility";
import VisibilityOff from "@material-ui/icons/VisibilityOff";
import IconButton from "@material-ui/core/IconButton";
import RememberMeCheckBox from "./RememberMeCheckBox/RememberMeCheckBox";

function CartCheckoutSignin() {
  const [showPassword, setShowPassword] = useState(false);
  const handleChange = () => {
    setShowPassword(!showPassword);
  };
  return (
    <>
      <div className="cartcheckoutsignin__body">
        <div className="cartcheckoutsignin__username">
          <input type="text" placeholder="Username"></input>
        </div>
        <div className="cartcheckoutsignin__password">
          <input
            type={showPassword ? "text" : "password"}
            placeholder="Password"
            autocomplete="off"
          ></input>
          {showPassword ? (
            <IconButton onClick={handleChange} style={{padding:"0"}}>
              <Visibility style={{padding:"0"}}/>
            </IconButton>
          ) : (
            <IconButton onClick={handleChange} style={{padding:"0"}}>
              <VisibilityOff style={{padding:"0"}}/>
            </IconButton>
          )}
        </div>
        <div className="cartcheckoutsignin__middle-forgotpassword">
          <RememberMeCheckBox text="Remember Me" />
          <h6 style={{color: "#b5818a", cursor:"pointer"}}>Forgot Password?</h6>
        </div>
        <div className="cartcheckoutsignin__login">LOGIN TO CHECKOUT</div>
        <div className="cartcheckoutsignin__login-asguest">CONTINUE TO GUEST</div>
        <div  className="cartcheckoutsignin__checkout">
          <div>Checkout using</div>
          <img
            src="https://sm.pcmag.com/pcmag_in/review/p/paypal/paypal_mb8k.png"
            width="60px"
          ></img>
        </div>
      </div>
    </>
  );
}

export default CartCheckoutSignin;
